import React from 'react'
import { Modal } from 'react-bootstrap'
import { useMediaQuery } from 'react-responsive'
import '../../../resources/styles/share-results.css'
import { Card } from '../../../types/card'
import { ComparationResult } from '../../../types/statistics'
import { snapdleNameSpace } from '../../../i18n/resources'
import { useTranslation } from 'react-i18next'
import { TFunction } from 'i18next'
import { getDayNumber } from './card-manager'
import TwitterLogo from '../../../resources/images/rrss/twitter.png'
import ClipboardLogo from '../../../resources/images/rrss/clipboard.png'
import { IconUrl, OverlayTooltip } from '../../rrss/social-media-bar'
import { GamePopup } from '../../utils/popup'
import { useGAController } from '../../utils/analytics'

const OK = "";
const KO = "";
const PARTIAL = "";
const MAX_ELEMENTS = 5
const URL = "https://snapdle.github.io/"
const TWITTER_URL = "https://twitter.com/intent/tweet?text=PAGE_TEXT&amp;url=PAGE_URL"

type ShareResultProps = {
    cardTries: Card[]
    cardOfTheDay: Card
    show: boolean
    setShow: (show: boolean) => void
}
const createShareUrl = (text: string) => {
    return TWITTER_URL.replace('PAGE_TEXT', encodeURIComponent(text).replaceAll('%2C', '')).replace('PAGE_URL', encodeURIComponent(URL))
}
const simpleComparator = (el1: any, el2: any) => el1 == el2 ? ComparationResult.OK : ComparationResult.KO
const arrayComparator = (el1: any[], el2: any[]) => el1.length == el2.length && el1.every((el: any) => el2.includes(el)) ?
    ComparationResult.OK : el1.some((el: any) => el2.includes(el)) ?
        ComparationResult.WARNNING : ComparationResult.KO
const dateComparator = (el1: Date, el2: Date) => {
    const tryCardDate = new Date(el1)
    const cardDate = new Date(el2)
    if (tryCardDate.getFullYear() != cardDate.getFullYear()) {
        return ComparationResult.KO
    } else if (tryCardDate.getMonth() != cardDate.getMonth()) {
        return ComparationResult.WARNNING
    } else {
        return ComparationResult.OK
    }
}

const comparationTable: Partial<{ [key in keyof Card]: ((el1: any, el2: any) => ComparationResult) }> =
{
    "name": simpleComparator,
    "abilities": arrayComparator,
    "categories": arrayComparator,
    "cost": simpleComparator,
    "power": simpleComparator,
    "source": simpleComparator,
    "releaseDate": dateComparator
};

const ResultAttribute: React.FC<{ comparationKey: keyof Card, cardTry: Card, cardOfTheDay: Card }> = (props) => {
    const { comparationKey, cardTry, cardOfTheDay } = props

    if (!comparationTable[comparationKey]) return <></>
    const result = (comparationTable[comparationKey] as any)(cardTry[comparationKey], cardOfTheDay[comparationKey])
    return <>{result == ComparationResult.OK ? OK : result == ComparationResult.WARNNING ? PARTIAL : KO}</>
}
const calculateLine = (cardTry: Card, cardOfTheDay: Card) => {
    return Object.keys(comparationTable).map((compKey) => {
        const comparationKey = compKey as keyof Card
        if (!comparationTable[comparationKey]) return <></>
        const result = (comparationTable[comparationKey] as any)(cardTry[comparationKey], cardOfTheDay[comparationKey])
        return result == ComparationResult.OK ? OK : result == ComparationResult.WARNNING ? PARTIAL : KO
    }
    )
}

const ResultRow: React.FC<{ cardTry: Card, cardOfTheDay: Card }> = (props) => {
    const { cardTry, cardOfTheDay } = props

    return <>{Object.keys(comparationTable).map((comparationKey) =>
        <ResultAttribute comparationKey={comparationKey as any} cardOfTheDay={cardOfTheDay}
            cardTry={cardTry} ></ResultAttribute>)
    }</>
}

const calculateResultLines = (t: TFunction<"snapdle", undefined>, cardTries: Card[], cardOfTheDay: Card) => {
    let result = [t('game.classic.results.winMessage', { day: getDayNumber(), tries: cardTries.length }),
    ...cardTries.filter((c, i) => cardTries.length == MAX_ELEMENTS + 1 || i < MAX_ELEMENTS)
        .map((cardTry, index) => {
            return calculateLine(cardTry, cardOfTheDay)
        })]
    if (cardTries.length != MAX_ELEMENTS + 1 && cardTries.length > MAX_ELEMENTS) {
        result = [...result, t('game.classic.results.triesMore', { moreTries: (cardTries.length - MAX_ELEMENTS) })]
    }
    return [...result, URL]
}

const ShareResultPopup: React.FC<ShareResultProps> = (props) => {
    const { t } = useTranslation(snapdleNameSpace)
    const { show, setShow, cardOfTheDay, cardTries } = props
    const { doInteraction } = useGAController('classic')

    const [isCopied, setIsCopied] = React.useState(false)

    const lines = calculateResultLines(t, cardTries, cardOfTheDay)

    const handleShow = (newShow: boolean) => {
        setShow(newShow)
        if (!newShow) {
            setIsCopied(false)
        } 
    };
    return <GamePopup show={show} setShow={handleShow}>
            <div className='text-center'>
                <h1>{t('game.classic.results.shareTitle')}</h1>
                <table className='table'>
                    <tbody>
                        {
                            lines.map((line, index) => {
                                return <tr key={index}>
                                    {line}
                                </tr>
                            })
                        }
                        <br />
                        <OverlayTooltip text={t('game.classic.share.twitter')}>
                            <IconUrl url={createShareUrl(lines.join('\n'))} logo={TwitterLogo} onClick={()=> doInteraction('ShareTwitter')}></IconUrl>
                        </OverlayTooltip>
                        <OverlayTooltip text={t('game.classic.share.clipboard')}>
                            <img  className='rrss-icon' style={{height: "45px", width: "45px"}} src={ClipboardLogo} onClick={() => {
                                navigator.clipboard.writeText(lines.join('\n').replaceAll(',', ''))
                                doInteraction('ShareClipboard')
                                setIsCopied(true)
                            }}></img>
                        </OverlayTooltip>

                        {isCopied? <div className='copied'>{t('game.classic.share.clipboardCoppied')}</div>:<></>}
                    </tbody>
                </table>
            </div>
        </GamePopup>
}

type ShareResultButtonProps = {
    cardTries: Card[]
    cardOfTheDay: Card
}
export const ShareResultButton: React.FC<ShareResultButtonProps> = (props) => {
    const { t } = useTranslation(snapdleNameSpace)
    const { doInteraction } = useGAController('classic')
    
    const { cardTries, cardOfTheDay } = props
    const [show, setShow] = React.useState(false)

    const handleShow = (newShow: boolean) => {
        setShow(newShow)
        if (show) {
            doInteraction('ShareResultOpen')
        }else {
            doInteraction('ShareResultClose')
        }
    }

    return <div>
        <button className='btn btn-primary marvel-text' onClick={() => handleShow(true)}>
            {t('game.classic.results.shareButton')}
        </button>
        <ShareResultPopup show={show} setShow={handleShow} cardTries={cardTries} cardOfTheDay={cardOfTheDay} />
    </div>
}
