import React from 'react'

import '../../../resources/styles/classic.css'
import { snapdleNameSpace } from '../../../i18n/resources'
import { useTranslation } from 'react-i18next'
import { GamePopup } from '../../utils/popup';
import { useGAController } from '../../utils/analytics';

export const Tutorial: React.FC = () => {
    const { t } = useTranslation(snapdleNameSpace)

    return (
        <div className="colour-guide marvel-text text-center">
            <h4>{t('game.classic.tutorial.title')}</h4>
            <h6>{t('game.classic.tutorial.line1')}</h6>
            <h6>{t('game.classic.tutorial.line2')}</h6>
            <div className="row">
                <div className="col colour-guide-item">
                    <div className='d-inline-flex'>
                        <i className="colour-guide-icon bg-success"><div className='color-guide-ok'></div></i>
                        <div className="colour-guide-text">{t('game.classic.tutorial.success')}</div>
                    </div>
                </div>
                <div className="col-5 colour-guide-item">
                    <div className='d-inline-flex'>
                        <i className="colour-guide-icon bg-warning "><div className='color-guide-partial'></div></i>
                        <div className="colour-guide-text">{t('game.classic.tutorial.partial')}</div>
                    </div>
                </div>
                <div className="col colour-guide-item">
                    <div className='d-inline-flex'>
                        <i className="colour-guide-icon bg-danger "><div className='color-guide-ko'></div></i>
                        <div className="colour-guide-text">{t('game.classic.tutorial.fail')}</div>
                    </div>
                </div>
            </div>
            <br />
            <div>
                {t('game.classic.tutorial.partialDescriptionPrev')}
                <span className='text-decoration-underline'>{t('game.classic.tutorial.partialDescription')}</span>
                {t('game.classic.tutorial.partialDescriptionPost')}
            </div>
        </div>
    )
}


export type TutorialPopupProps = {
    show: boolean
    setShow: (show: boolean) => void
}

export const TutorialPopup: React.FC<TutorialPopupProps> = (props) => {
    const { show, setShow } = props

    const handleClose = (newShow: boolean) => setShow(newShow);
    return <GamePopup show={show} setShow={handleClose} >
        <Tutorial />
    </GamePopup>
}

export const TutorialButton: React.FC<{}> = () => {
    const { t } = useTranslation(snapdleNameSpace)
    const [show, setShow] = React.useState(false)
    const { doInteraction } = useGAController('classic')

    const handleShow = (newShow: boolean) => {
        setShow(newShow);
        if(show) {
            doInteraction('TutorialOpen')
        }else {
            doInteraction('TutorialClose')
        }
    }

    return <div>
        <button className='btn btn-primary marvel-text' onClick={() => handleShow(true)}>
            {t('game.classic.tutorialButton')}
        </button>
        <TutorialPopup show={show} setShow={handleShow} />
    </div>
}
